body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.encabezado{
display: flex;
flex-flow: row wrap;
justify-content: space-between;
background-color: black;
height: 15vh;
color: white;
padding: 0.5em 1.5em;
}

.encabezado h1{
  font-weight: 500;
  font-size: 2em;
}

.nUsuario{
  margin-left: 38em;
  margin-top: 2em;
}

.imagen{
  width: 4.5em;
  margin-top: 1em;
}

.section-parrafo p{
  padding: 0.5em 2em;
  height: 3em;
  
}

/* --------------------- Contenedor section ------------------------- */ 

.section-parrafo{
  height: 70.8vh;
  display: flex;
  align-items: center;
  flex-direction: column;
}

/* ------------------------ formulario -----------------------------*/

.contenedor-formulario{
  height: 60vh;
  width: 100%;
  
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;


}
.formulario {
  height: 70%;
  background-color: rgb(207, 3, 105);
  width: 30%;
  margin: 0 1.5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1em;
  
}

.formulario div{ 
  
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 50%;
  margin-top: 0.5em;
  
}
.formulario div input{ 
  height: 2em;
  border-radius: 0.4em;
  
}
.formulario .boton{ 
  width: 50%;
  height: 2.5em;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 1em;
  background-color: black;
  color: white;
  border-radius: 0.7em;
  cursor: pointer;

  
}



/* ---************-------------------tabla---------------------------****************----- */







.contenedorTabla{
  width: 50%;
  margin-right: 5em;

}

.tabla{
  
  width: 100%;
  border: 0.2em solid black;
  
}

.tablaCabeza{
  
  margin: 0.2em;
  border-bottom: black solid 0.1em;

}

.tablaCabeza th{
height: 3em;
display: flex;
align-items: center;


 
}

.contenedorThead{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 0.5em;
  
}

.botonDelete{
  padding: 0.6em 1.2em;
  background-color: rgb(245, 101, 101);
  font-weight: 600;
  border-radius: 0.5em;
  cursor: pointer;
}

/* ----------------------- contenedor table body ---------------------- */

.tbody td{
height: 2em;
width: 50%;
padding: 0.8em;

  
}

.tbody{
  
  margin: 0.2em;
  
}



/* ---------------------footer--------------------- */

.el-footer{
  background-color: black;
  height:9vh;
  
  text-align: center;
}
.el-footer p{
  color: white;
  padding-top: 1em;
}